import React, { useState, useEffect, useRef } from "react";
import ReactMarkdown from "react-markdown";
import emailjs from "emailjs-com";
import "./DayModal.css";

function DayModal({ date, content = {}, onClose }) {
  const [comment, setComment] = useState("");
  const [sending, setSending] = useState(false);
  const [sent, setSent] = useState(false);
  const [answers, setAnswers] = useState([]);
  const audioRef = useRef(null);

  // Prevent background scroll when modal is open
  useEffect(() => {
    document.body.classList.add("modal-open");
    return () => {
      document.body.classList.remove("modal-open");
    };
  }, []);

  useEffect(() => {
    if (date === "2025-05-25" && audioRef.current) {
      audioRef.current.currentTime = 0;
      audioRef.current.play().catch(() => {});
    }
  }, [date]);

  useEffect(() => {
    if (date) {
      const saved = localStorage.getItem(`comment-${date}`);
      setComment(saved || "");
      setSent(false); // Reset sent state on new day

      // Initialize answers for questions, if present
      if (content?.questions) {
        const savedAnswers = JSON.parse(localStorage.getItem(`answers-${date}`) || "[]");
        setAnswers(
          savedAnswers.length === content.questions.length
            ? savedAnswers
            : Array(content.questions.length).fill("")
        );
      } else {
        setAnswers([]);
      }
    }
  }, [date, content?.questions]);

  // Handle answer changes for multi-question days
  const handleAnswerChange = (idx, value) => {
    setAnswers((prev) =>
      prev.map((ans, i) => (i === idx ? value : ans))
    );
    // Optionally save to localStorage for persistence
    const updated = answers.map((ans, i) => (i === idx ? value : ans));
    localStorage.setItem(`answers-${date}`, JSON.stringify(updated));
  };

  // Handle multi-question submit
  const handleSubmitAnswers = () => {
    if (!answers.some((ans) => ans.trim())) return; // Require at least one answer
    setSending(true);

    // Combine answers for emailjs
    const combined = content.questions
      .map((q, i) => `Q: ${q}\nA: ${answers[i] || "(no answer)"}`)
      .join("\n\n");

    // Always send both answers and comment (comment is empty here)
    emailjs
      .send(
        "service_f69boyh", // Replace with your EmailJS Service ID
        "template_70m09zt", // Replace with your EmailJS Template ID
        {
          date,
          dayTitle: content.title || `Surprise for ${date}`,
          answers: combined,
          comment: "",
        },
        "L5_gvKb1_pNR5lliD" // Replace with your EmailJS User ID (public key)
      )
      .then(
        () => {
          setSending(false);
          setSent(true);
        },
        () => {
          setSending(false);
          alert("Failed to send. Please try again.");
        }
      );
  };

  // Handle single comment (legacy/fallback)
  const handleCommentChange = (e) => {
    setComment(e.target.value);
    localStorage.setItem(`comment-${date}`, e.target.value);
  };

  const handleSend = () => {
    if (!comment.trim()) return;
    setSending(true);
    // Always send both answers and comment (answers is empty here)
    emailjs
      .send(
        "service_f69boyh", // Replace with your EmailJS Service ID
        "template_vncp1yt", // Replace with your EmailJS Template ID
        {
          date,
          dayTitle: content.title || `Surprise for ${date}`,
          answers: "",
          comment,
        },
        "L5_gvKb1_pNR5lliD" // Replace with your EmailJS User ID (public key)
      )
      .then(
        () => {
          setSending(false);
          setSent(true);
        },
        () => {
          setSending(false);
          alert("Failed to send. Please try again.");
        }
      );
  };

  if (!date || !content) return null;

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div
        className="modal-content"
        onClick={(e) => e.stopPropagation()}
        style={{
          width: "90vw",
          maxWidth: 400,
          maxHeight: "90vh",
          overflowY: "auto",
          boxSizing: "border-box",
          position: "relative",
          background: "#18121b",
          color: "#fff",
          borderRadius: 16,
          padding: "32px 24px 24px 24px",
        }}
      >
        <button className="modal-close" onClick={onClose} aria-label="Close modal">
          ×
        </button>
        <h2>{content.title || `Surprise for ${date}`}</h2>

        {/* Main intro text at the top */}
        {content?.text &&
          content.text.split("\n\n").map((para, idx) => (
            <p key={`intro-text-${idx}`} style={{ marginBottom: "1.2em", whiteSpace: "pre-line" }}>
              {para}
            </p>
          ))}

        {/* Interleaved blocks (Markdown for text, plus images/videos) */}
        {content?.blocks &&
          content.blocks.map((block, idx) => {
            if (block.type === "text") {
              return (
                <ReactMarkdown
                  key={idx}
                  children={block.value}
                  components={{
                    p: ({ node, ...props }) => (
                      <p style={{ marginBottom: "1.2em", whiteSpace: "pre-line" }} {...props} />
                    ),
                  }}
                />
              );
            }
            if (block.type === "image") {
              return (
                <img
                  key={idx}
                  src={block.src}
                  alt={block.alt || ""}
                  style={{
                    maxWidth: "100%",
                    borderRadius: "12px",
                    margin: "16px 0",
                    boxShadow: "0 2px 8px #0002",
                  }}
                />
              );
            }
            if (block.type === "video") {
              return (
                <video
                  key={idx}
                  src={block.src}
                  controls
                  style={{
                    maxWidth: "100%",
                    borderRadius: "12px",
                    margin: "16px 0",
                    boxShadow: "0 2px 8px #0002",
                  }}
                >
                  Your browser does not support the video tag.
                </video>
              );
            }
            return null;
          })}

        {/* Multi-question form */}
        {content?.questions && (
          <form
            onSubmit={e => {
              e.preventDefault();
              handleSubmitAnswers();
            }}
            style={{ marginTop: 24 }}
          >
            {content.questions.map((q, idx) => (
              <div key={idx} style={{ marginBottom: 16 }}>
                <label style={{ fontWeight: "bold", display: "block", marginBottom: 4 }}>
                  {q}
                </label>
                <textarea
                  value={answers[idx] || ""}
                  onChange={e => handleAnswerChange(idx, e.target.value)}
                  style={{
                    width: "100%",
                    minHeight: "40px",
                    borderRadius: "6px",
                    padding: "8px",
                    fontSize: "1rem",
                    marginBottom: "6px"
                  }}
                  placeholder="Your answer..."
                  disabled={sent}
                />
              </div>
            ))}
            <button
              type="submit"
              disabled={sending || sent || answers.every(ans => !ans.trim())}
              style={{
                width: "100%",
                background: sent ? "#b6ffb6" : "#ffb7c5",
                color: "#2d1e2f",
                border: "none",
                borderRadius: "8px",
                padding: "10px",
                fontWeight: "bold",
                cursor: sending || sent || answers.every(ans => !ans.trim()) ? "not-allowed" : "pointer"
              }}
            >
              {sent ? "Sent!" : sending ? "Sending..." : "Submit Answers"}
            </button>
          </form>
        )}

        {/* Fallback: single comment box */}
        {!content?.questions && (
          <>
            <textarea
              placeholder="Leave a comment for Javi"
              value={comment}
              onChange={handleCommentChange}
              style={{
                width: "100%",
                minHeight: "60px",
                marginTop: "12px",
                boxSizing: "border-box",
                background: "#ffe4ec",
                color: "#2d1e2f",
                border: "1.5px solid #ffb7c5",
                borderRadius: "8px",
                padding: "10px",
                fontSize: "1rem",
                fontFamily: "inherit",
                resize: "vertical",
                boxShadow: "0 2px 8px #ffb7c544",
                transition: "border-color 0.2s"
              }}
              disabled={sent}
            />
            <button
              onClick={handleSend}
              disabled={sending || sent || !comment.trim()}
              style={{
                width: "100%",
                marginTop: "10px",
                background: sent ? "#b6ffb6" : "#ffb7c5",
                color: "#2d1e2f",
                border: "none",
                borderRadius: "8px",
                padding: "8px 18px",
                fontWeight: "bold",
                cursor: sending || sent || !comment.trim() ? "not-allowed" : "pointer",
                transition: "background 0.2s"
              }}
            >
              {sent ? "Sent!" : sending ? "Sending..." : "Send"}
            </button>
          </>
        )}

        {/* Optional: Audio for special days */}
        {date === "2025-05-25" && (
          <audio
            ref={audioRef}
            src={process.env.PUBLIC_URL + "/audio/twin-peaks-intro.mp3"}
            autoPlay
            preload="auto"
          />
        )}
      </div>
    </div>
  );
}

export default DayModal;
